import {Directive, Input, TemplateRef, Output, EventEmitter, ContentChild} from '@angular/core';
import {NglTab} from './tab';

/*
 * <ngl-tab [heading="..."]>
 *    <template ngl-tab-heading>...</template>
 *    <template ngl-tab-content>
 *       Content goes here...
 *    </template>
 * </ngl-tab>
 */

export class NglTabHeading {
  constructor(public templateRef: TemplateRef<any>) {}
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: 'template[ngl-tab-heading]'}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: TemplateRef, },
];
}


export class NglTabContent {
  constructor(public templateRef: TemplateRef<any>) {}
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: 'template[ngl-tab-content]'}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: TemplateRef, },
];
}


export class NglTabVerbose extends NglTab {
   id: string;
   heading: string | TemplateRef<any>;
   onActivate = new EventEmitter<NglTab>();
   onDeactivate = new EventEmitter<NglTab>();

   contentTemplate: NglTabContent;
   headingTemplate: NglTabHeading;

  ngAfterContentInit() {
    if (this.headingTemplate) {
      this.heading = this.headingTemplate.templateRef;
    }
    this.templateRef = this.contentTemplate.templateRef;
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: 'ngl-tab',
  providers: [ {provide: NglTab, useExisting: NglTabVerbose} ],
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'id': [{ type: Input, args: ['nglTabId', ] },],
'heading': [{ type: Input },],
'onActivate': [{ type: Output },],
'onDeactivate': [{ type: Output },],
'contentTemplate': [{ type: ContentChild, args: [NglTabContent, ] },],
'headingTemplate': [{ type: ContentChild, args: [NglTabHeading, ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
